* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: border-box;
}

a,
a:visited {
  text-decoration: none;
  font-family: 'StratosSkyeng', sans-serif;
  cursor: pointer;
}

button,
._btn {
  cursor: pointer;
}

ul li {
  list-style: none;
}

//-------------------------------//

// ========== fonts ========
@font-face {
  font-family: 'StratosSkyeng';
  src: local('StratosSkyeng'), local('StratosSkyeng'),
    url('./fonts/Stratos-Black.woff2') format('woff2'),
    url('./fonts/Stratos-Black.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

//--------------------------

body {
  width: 100%;
  height: 100%;
  font-family: 'StratosSkyeng', sans-serif;
  background: var(--primaryBackground);
  color: var(--primaryTextColor);
}

html {
  --primaryBackground: white;
  --primaryTextColor: black;
  --primaryBackgroundNav: #f6f5f3;
}

html[data-theme='dark'] {
  --primaryBackgroundNav: #181818;
  --primaryBackground: #181818;
  --primaryTextColor: white;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  background-color: #383838;
}

.container {
  max-width: 1920px;
  height: 100vh;
  margin: 0 auto;
  position: relative;
  background-color: var(--primaryBackground);
  &__auth {
    max-width: 1920px;
    height: 100vh;
    margin: 0 auto;
    position: relative;
    background-color: black;
  }
}

// ========== hover ========
._btn-text:hover {
  border-color: #d9b6ff;
  color: #d9b6ff;
  cursor: pointer;
}

._btn-icon:hover svg {
  fill: transparent;
  stroke: #acacac;
  cursor: pointer;
}

// ========== activ ========

.active {
  color: #ad61ff !important;
  text-decoration: underline;
}

._btn-text:active {
  border-color: #ad61ff;
  color: #ad61ff;
  cursor: pointer;
}

._btn-icon:active svg {
  fill: transparent;
  stroke: #ffffff;
  cursor: pointer;
}

._btn-icon:active .track-play__like-svg,
._btn-icon:active .track-play__dislike-svg {
  fill: #696969;
  stroke: #ffffff;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 4px;
  height: 237px;
  background-color: #4b4949;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  width: 4px;
  height: 65px;
  background: #ffffff;
  border-radius: 10px;
}

// =================== main ====================//
.main {
  flex: 1 1 auto;
  display: flex;
  // flex-wrap: wrap;
  justify-content: space-between;

  &__nav {
    width: 244px;
    height: 100vh;
    background-color: var(--primaryBackgroundNav);
    padding: 20px 0 20px 36px;
  }

  &__centerblock {
    width: auto;
    flex-grow: 3;
    padding: 20px 40px 20px 111px;
  }

  &__sidebar {
    max-width: 418px;
    padding: 20px 90px 20px 78px;
  }
}

.nav {
  &__logo {
    width: 113.33px;
    height: 43px;
    padding: 13px 0 13px 0;
    background-color: transparent;
    margin-bottom: 20px;
  }

  &__burger {
    width: 20px;
    height: 36px;
    padding: 13px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  :hover {
    cursor: pointer;
  }

  &__menu {
    display: block;
    visibility: visible;
  }
}

.logo {
  &__image {
    width: 113.33px;
    height: 17px;
    color: #181818;
  }
}

.burger {
  &__line {
    display: inline-block;
    width: 100%;
    height: 1px;
    background-color: var(--primaryTextColor);
  }
}

.menu {
  &__list {
    padding: 18px 0 10px 0;
  }

  &__item {
    padding: 5px 0;
    margin-bottom: 16px;
  }

  &__link {
    color: var(--primaryTextColor);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }
}

.centerblock {
  &__search {
    width: 100%;
    border-bottom: 1px solid;
    margin-bottom: 51px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__h2 {
    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 72px;
    letter-spacing: -0.8px;
    margin-bottom: 45px;
  }

  &__filter {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 51px;
  }

  &__content {
    display: flex;
    flex-direction: column;
  }
}

.search {
  &__svg {
    width: 17px;
    height: 17px;
    margin-right: 5px;
  }

  &__text {
    flex-grow: 100;
    background-color: transparent;
    border: none;
    padding: 13px 10px 14px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--primaryTextColor);

    &::placeholder {
      background-color: transparent;
      color: var(--primaryTextColor);
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
    }
  }
}

.filter {
  &__title {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    margin-right: 15px;
  }

  &__button {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    border: 1px solid var(--primaryTextColor);
    border-radius: 60px;
    padding: 6px 20px;
  }

  &__link-author {
    color: var(--primaryTextColor);
    padding-top: 24px;
    font-size: 20px;
  }

  &-year {
    position: absolute;
    width: 403px;
    height: 92px;
    background: #313131;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 14px;
    margin-top: 10px;
  }
  &-author {
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
  }
  &__wrap {
    margin-right: 10px;
  }
}

.box {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  position: absolute;
  padding: 34px;
  width: 248px;
  height: 305px;
  background: #313131;
  border-radius: 12px;
}

.radio {
  display: none;
}

.fake {
  width: 18px;
  height: 18px;
  border: 1px solid white;
  border-radius: 50%;
  position: relative;
}

.fake::before {
  content: '';
  width: 10px;
  height: 10px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: 0.2s;
}

.radio:checked + .fake::before {
  opacity: 1;
}

.content {
  &__title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__playlist {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }
}

.playlist-title {
  &__col {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 2px;
    color: #696969;
    text-transform: uppercase;
  }

  &__svg {
    width: 12px;
    height: 12px;
    fill: transparent;
    stroke: #696969;
  }
}

.col01 {
  width: 447px;
}

.col02 {
  width: 321px;
}

.col03 {
  width: 245px;
}
.col04 {
  width: 60px;
  text-align: end;
}

.playlist {
  &__item {
    width: 100%;
    display: block;
    margin-bottom: 12px;
  }

  &__track {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.track {
  &__title {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 447px;
  }

  &__title-image {
    width: 51px;
    height: 51px;
    padding: 16px;
    background: #313131;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 17px;
  }

  &__title-svg {
    width: 18px;
    height: 17px;
    fill: transparent;
    stroke: #4e4e4e;
  }

  &__title-link {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--primaryTextColor);
  }

  &__title-span {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #4e4e4e;
  }

  &__author {
    width: 321px;
    display: flex;
    justify-content: flex-start;
  }

  &__author-link {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--primaryTextColor);
    text-align: left;
  }

  &__album {
    width: 245px;
  }

  &__album-link {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #696969;
  }

  //&__time {}

  &__time-svg {
    width: 14px;
    height: 12px;
    margin-right: 17px;
    fill: transparent;
    stroke: #696969;
  }

  &__time-text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: right;
    color: #696969;
  }
}

.sidebar {
  &__personal {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    padding: 12px 0 15px 0;
  }

  &__personal-name {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--primaryTextColor);
    margin-right: 16px;
  }

  &__avatar {
    width: 43px;
    height: 43px;
    background-color: #313131;
    border-radius: 50%;
  }

  &__block {
    height: 100%;
    padding: 240px 0 0 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__item {
    width: 250px;
    height: 150px;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__link {
    width: 100%;
    height: 100%;
  }

  &__img {
    width: 100%;
    height: auto;
  }
}

// =================== bar-player ====================//

.bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: var(--primaryBackground);
}

.bar {
  &__content {
    display: flex;
    flex-direction: column;
  }

  &__player-progress {
    width: 100%;
    height: 5px;
    cursor: pointer;
    // background: #2e2e2e;
    background: -webkit-linear-gradient(
      left,
      #8870ff 0%,
      #8870ff 25%,
      #2e2e2e 25%,
      #2e2e2e 100%
    );
    // appearance: none;
    // &::-webkit-slider-thumb {
    //   appearance: none;
    // }
  }

  &__player-block {
    height: 73px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &__player {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }

  &__volume-block {
    width: auto;
    display: flex;
    align-items: center;
    padding: 0 92px 0 0;
  }
}

.player {
  &__controls {
    display: flex;
    flex-direction: row;
    padding: 0 27px 0 31px;
  }

  &__btn-prev,
  &__btn-play,
  &__btn-next,
  &__btn-repeat,
  &__btn-shuffle {
    padding: 5px;
    display: flex;
    align-items: center;
  }

  &__btn-prev {
    margin-right: 23px;
  }

  &__btn-prev-svg {
    width: 15px;
    height: 14px;
  }

  &__btn-play {
    margin-right: 23px;
  }

  &__btn-play-svg {
    width: 22px;
    height: 20px;
    fill: #d9d9d9;
  }

  &__btn-next {
    margin-right: 28px;
    fill: #a53939;
  }

  &__btn-next-svg {
    width: 15px;
    height: 14px;
    fill: inherit;
    stroke: #d9d9d9;
  }

  &__btn-repeat {
    margin-right: 24px;
  }

  &__btn-repeat-svg {
    width: 18px;
    height: 12px;
    fill: transparent;
    stroke: #696969;
  }

  &__btn-shuffle {
    display: flex;
    align-items: center;
  }

  &__btn-shuffle-svg {
    width: 19px;
    height: 12px;
    fill: transparent;
    stroke: #696969;
  }

  &__track-play {
    display: flex;
    flex-direction: row;
  }
}

.track-play {
  &__contain {
    width: auto;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-areas: 'image author' 'image album';
    align-items: center;
  }

  &__image {
    width: 51px;
    height: 51px;
    background-color: #313131;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 12px;
    grid-area: image;
  }

  &__svg {
    width: 18px;
    height: 17px;
    fill: transparent;
    stroke: #4e4e4e;
  }

  &__author {
    grid-area: author;
    min-width: 49px;
  }

  &__author-link {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--primaryTextColor);
    white-space: nowrap;
  }

  &__album {
    grid-area: album;
    min-width: 49px;
  }

  &__album-link {
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 24px;
    color: var(--primaryTextColor);
  }

  &__like-dis {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 26%;
  }

  &__like,
  &__dislike {
    padding: 5px;
  }

  &__like-svg {
    width: 14px;
    height: 12px;
    fill: transparent;
    stroke: #696969;
  }

  &__dislike {
    margin-left: 28.5px;
  }

  &__dislike-svg {
    width: 14.34px;
    height: 13px;
    fill: transparent;
    stroke: #696969;
  }
}

.volume {
  &__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: end;
  }

  &__image {
    width: 13px;
    height: 18px;
    margin-right: 17px;
  }

  &__svg {
    width: 13px;
    height: 18px;
    fill: transparent;
  }

  &__progress {
    width: 109px;
  }

  &__progress-line {
    width: 109px;
  }
}

.form {
  display: flex;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;

  &__auth {
    width: 366px;
    height: 439px;
    border-radius: 12px;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 43px;
    gap: 30px;
  }

  &__input {
    border: none;
    border-bottom: 1px solid #d0cece;
    padding: 8px;
    padding-left: 0;
    width: 278px;
    outline: none;
    font-size: 18px;
    &::placeholder {
      font-size: 18px;
      color: #e1e1e1;
    }
  }

  &__login {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 278px;
    height: 52px;
    color: #fff;
    border-radius: 6px;
    background: #580ea2;
    margin-top: 30px;
    &:hover {
      background: #3f007d;
    }
  }
  &__register {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 278px;
    height: 52px;
    color: #000;
    border: 1px solid #d0cece;
    border-radius: 6px;
    &:hover {
      background: #d0cece;
    }
  }
}
